"use client";

import React, {useState} from 'react'



const SignInPage = () => {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');

    const handleSubmit = (e: any) => {
        e.preventDefault();
        console.log({username, password});
    }
    const userIcon = '/userIcon.png';
    const passwordIcon = '/passwordIcon.png';

    return (
        <div className="flex-center flex-1 h-full w-full">
            <div className="flex flex-row w-[800px] h-[500px] shadow-2xl rounded-lg border-gray-200 border-2">
                <div className="w-1/2 h-full border-2 border-amber-50 flex-center">
                    <form onSubmit={handleSubmit} className="py-3 space-y-5">
                        {/*Username */}
                        <InfoLine
                            placeholder="Username"
                            value={username}
                            onChangeFunc={(e) => setUsername(e.target.value)}
                            icon={userIcon}
                        />

                        {/*Password*/}
                        <InfoLine
                            placeholder="Password"
                            value={password}
                            onChangeFunc={(e) => setPassword(e.target.value)}
                            icon={passwordIcon}
                        />
                    </form>
                </div>
                <div className="w-1/2 h-full border-2 border-amber-50 bg-dark-400 "></div>
            </div>

        </div>
    )
}
export default SignInPage;


